<template>
    <div class="container">
        <h1>{{ news.title }}</h1>
        <div>消息類型: {{ news_type }} | 發布者: {{ news.editor }}</div>
        <div class="vue-html" v-html="news.context"></div>
        <div class="text-center">
            公告日期: {{ moment(news.created_at).format("YYYY-MM-DD HH:MM") }} | 
            最後更新日期: {{ moment(news.updated_at).format("YYYY-MM-DD HH:MM") }}
        </div>
        <div class="text-center">
            <button type="button" class="btn btn-secondary my-3" @click="historyPrev">回上頁</button>
        </div>
    </div>
</template>

<script>
import { NewsServer } from "../../providers/http-server.js";
import moment from 'moment';
import '../../libs/ckeditor_plugin.css';

export default {
    name: 'NewsDetail',
    data(){
        return {
            news: [],
            news_type: ''
        }
    },
    created(){
        this.moment = moment;
        NewsServer.item(this.$route.params.id).then(
            (response) => {
                this.news = response.data;
                this.news_type = response.data.notification.type;
            }
        )
    },
    methods: {
        historyPrev() {
            this.$router.go(-1);
        }
    }
}
</script>

<style scoped>
@import '../../libs/ckeditor_plugin.css';
</style>